.contactSection {
  padding: 4rem 1rem;
  background: var(--secondary-color);
}

.title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  position: relative;
}
.title::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background: var(--accent-color);
  margin: 8px auto 0;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
}

.card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  transition: transform 0.2s, box-shadow 0.2s;
  padding: 1.5rem;
  width: 280px;
  text-align: center;
}
.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 16px rgba(0,0,0,0.1);
}

.cardTitle {
  margin: 0 0 0.5rem;
  font-size: 1.1rem;
  color: var(--text-color);
}

.link {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
}
.link:hover {
  text-decoration: underline;
}