.experienceSection {
  padding: 4rem 1rem;
  background: var(--secondary-color);
}

.title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
  position: relative;
}
.title::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background: var(--accent-color);
  margin: 8px auto 0;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 1.5rem;
  justify-items: center;
}

.card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  transition: transform 0.2s, box-shadow 0.2s;
  width: 100%;
  max-width: 600px;
}
.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 16px rgba(0,0,0,0.1);
}

.cardHeader {
  background: var(--primary-color);
  color: white;
  padding: 1rem 1.5rem;
  border-radius: 8px 8px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.company {
  margin: 0;
  font-size: 1.25rem;
}

.period {
  font-size: 0.9rem;
  opacity: 0.85;
}

.cardBody {
  padding: 1.5rem;
}

.cardBody p {
  margin-bottom: 1rem;
  line-height: 1.5;
}

.list {
  list-style: none;
  padding: 0;
}
.list li {
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 1.5rem;
  line-height: 1.4;
}
.list li::before {
  content: '⚡';
  position: absolute;
  left: 0;
  top: 0;
  color: var(--accent-color);
}